@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@200;400;800&display=swap);
.movieShow {
    display: flex;
    flex-direction: column;
    width: 20rem;
    overflow: hidden;
    position: relative;
}

.movieShow img {
    width: 100%;
    margin: auto;
    object-fit: cover;
    align-self: center;
    border: 0.1rem solid #010101;
}

.movieShow img:hover {
    -webkit-filter: grayscale(0);
            filter: grayscale(0);
    -webkit-transform: scale(0.99);
            transform: scale(0.99);
    opacity: 1;
    transition: all ease 0.4s;
    border: 0.1rem solid greenyellow;
}

.Yes {
    opacity: 0.35;
    -webkit-filter: grayscale(1);
            filter: grayscale(1);
}

.movieShow h2 {
    padding: 1rem;
    margin: 0;
    align-self: center;
    text-align: center;
    line-height: 1;
}

.overviewInfo {
    position: absolute;
    opacity: 0.9;
    background-color: #111;
    bottom: 0;
    left: 0;
    right: 0;
    max-height: 100%;
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
    z-index: -1;
}

.movieShow:hover .overviewInfo {
    cursor: pointer;
    transition: all ease 0.5s;
    z-index: 99;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
}

.overviewInfo p {
    padding: 2rem;
    font-size: 1rem;
    margin: 0;
    align-self: center;
    text-align: justify;
}
html {
    font-size: 62.5%;
}

html,
body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: #010101;
    color: whitesmoke;
    font-family: "Outfit", sans-serif;
    width: 100vw;
}

.page {
    width: 100%;
    margin: 0;
}

nav {
    display: flex;
    justify-content: space-around;
    max-height: 10rem;
    padding: 1rem 0;
}

#logo {
    height: 100%;
    padding: 0 6rem;
}

img:hover {
    cursor: pointer;
}

nav {
    justify-content: space-between;
    position: fixed;
    z-index: 99;
    top: 0;
    background-color: #010101;
    width: 100vw;
}

nav ul {
    display: flex;
    width: 60%;
    grid-gap: 5rem;
    gap: 5rem;
    list-style: none;
    margin: 0;
    padding: 0 5rem;
}

nav ul li {
    align-self: center;
    font-size: 1.6rem;
}

nav ul li:hover {
    color: greenyellow;
    cursor: pointer;
}

.btn {
    width: 100px;
    font-weight: 800;
    background-color: greenyellow;
    border: 0.1rem solid greenyellow;
}

.btn:hover {
    cursor: pointer;
    color: #111;
    background-color: greenyellow;
}

section {
    margin-top: 15rem;
    display: flex;
    flex-wrap: wrap;
    grid-gap: 1.5rem;
    gap: 1.5rem;
    width: 100vw;
    justify-content: center;
}
